.input-page {
  padding: 2rem;
  position: relative;
  background: linear-gradient(to bottom right, #ffffff, #ecf1cc);
  min-height: 100vh; /* 背景が全体に届くように */
}

/* ハンバーガーアイコン */
.hamburger {
  font-size: 24px;
  cursor: pointer;
  user-select: none;
  margin-bottom: 10px;
  padding: 4px 8px;
  display: inline-block;
  background-color: transparent;
  border: none;
}

/* メニュー */
.input-page .menu {
  position: absolute;
  top: 45px;
  left: 10px;
  background-color: white;
  border: 1px solid #ccc;
  padding: 10px;
  z-index: 10;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.2);
  display: flex;
  flex-direction: column;
  gap: 8px;
  width: 160px;
}

/* 通常のメニューボタン（トップに戻る） */
.input-page .menu-button {
  background-color: transparent;
  color: #333;
  border: none;
  padding: 10px;
  border-radius: 4px;
  cursor: pointer;
  text-align: left;
  width: 100%;
  font-size: 14px;
  font-weight: bold;
  transition: background-color 0.2s;
}

.input-page .menu-button:hover {
  background-color: #f0f0f0;
}

/* ログアウト専用ボタン */
.input-page .menu-button.logout {
  background-color: #e74c3c;
  color: white;
}

.input-page .menu-button.logout:hover {
  background-color: #c0392b;
}

/* 「空枠情報」ボタン */
.input-page .vacancy-button {
  background-color: #4285F4;
  color: white;
  padding: 10px 20px;
  margin-top: 20px;
  font-size: 16px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.input-page .vacancy-button:hover {
  background-color: #03327c;
}
