code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.login-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  padding: 40px;
  background: linear-gradient(to bottom right, #ffffff, #ecf1cc);
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  text-align: center;
  
  
}

.login-logo {
  width: 280px;
  margin-bottom: 60px;
  background-color: transparent

}

h2 {
  margin-bottom: 20px;
  color: #03327c;
  font-size: 24px;
}

.google-login-button {
  background-color: #4285F4;
  color: white;
  border: none;
  padding: 12px 32px;
  border-radius: 6px;
  font-size: 16px;
  font-weight: bold;
  box-shadow: 0 4px 8px rgba(66, 133, 244, 0.3);
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.google-login-button:hover {
  background-color: #03327c;
  transform: translateY(-5px);
}

.loading-text {
  margin-top: 20px;
  color: #666;
  font-size: 14px;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
